/* Rest of your CSS styles here */
/* Style the card */
.card {
  width: 300px;
  height: 400px;
  perspective: 1000px;
  margin: 20px;
  font-family: 'Poppins';
  font-size: 24px;
}

.card-inner {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.5s;

}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.button1{
background-color:  #333;
color: #fff;
padding: 5px 10px 5px 10px;
border-radius: 10%;
font-size: 15px;
font-family: 'poppins';
border: none;
margin: 2.5px;
}


.card-title {
  display: flex;
  justify-content: center; /* Horizontal centering */
  align-items: center; /* Vertical centering */
  height: 200px;
  width: 200px;
  background-color: rgb(72, 176, 176);
  border-radius: 50%;
  font-size: 35px;
  font-family: 'Poppins';
}





/* Style the front and back of the card */
.card-front,
.card-back {
  width: 100%;
  height: 100%;
  background-color: rgb(17, 17, 85); 
  color: #ffffff;
  position: absolute;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
  text-align: center;
  font-size: 1.2rem;
}


.card-back {
  transform: rotateY(180deg);
}

/* Add styles for the footer */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #2449b0; /* Set your desired background color */
  color: #fff; /* Set your desired text color */
  padding: 10px; /* Adjust padding as needed */
  text-align: center;
  font-family: 'Poppins';
}

